<template>
    <div :id="id" class="card" :draggable="draggable" @dragstart="dragStart" @dragover.stop @dragend.once="$emit('modal-show')" @dblclick="$emit('modal-appear')"> <!-- @dragend.once="dragEnd"-->
        <h5>{{ title }}</h5>
    </div>
</template>

<script>
export default {
    name: 'Card',
    props: ['id', 'draggable', 'title'],
    methods: {
        dragStart: e => {
            const target = e.target;
            e.dataTransfer.setData('card_id', target.id);
            //e.dataTransfer.effectAllowed = "copy";
            setTimeout(() => {
                target.style.display = "block";
            }, 0);
        },

        /*
        dragEnd: e => {
            //alert('This will be replaced by a modal with editing capabilities');
            //this.$emit('card-dropped');
        }
        */
    },

    emits: ['modal-show','modal-appear'],

}
</script>

<style scoped>
.card {
    display: flex;
    width: 100%;
    height: 30px;
    overflow: hidden;
    border-radius: 3px;
    padding: 5px;
    color: white;
    background-color: steelblue;
    cursor: pointer;
    margin-bottom: 10px;
}
</style>